
.record {
	    font-size: 1.6vw;
}
.record div.head {
	    position: absolute;
	    top: 0rem;
    	left: 230px;
}
.record div.head .back {
    top: 50%;
    left: 0;
    -webkit-transform: translateY(-45%);
    transform: translateY(-45%);
}
.cov-date-body{
		top: 46%!important;
}
.record-head{
		/*margin-top: 80px;*/
		padding: 20px 10px;
    padding-left: 340px;
}
.record-head-left{
    margin-left: 20px;
    margin-right: 10px;
}
.record-head>div {
    display: inline-block;
    margin-right: 10px;
}
.record .name-text{
		display: inline-block;
    height: 40px;
    margin-left: 10px;
}
.record-list{
		overflow: auto;
}
.record .name-text input{
		height: 40px;
    width: 150px;
    border: 1px solid #eee;
    padding-left: 10px;
}
.record .time,.record .status-text{
		display: inline-block;
		margin-left: 10px;
}
.status-select{
		outline: none;
    border: 0;
    background: #fff;
    border: 1px solid #eee;
    height: 40px;
    width: 150px;
    padding-left: 10px;
}
.record-content{
	    padding: 0px 2%;
	   	padding-left: 230px;
	   	padding-right: 30px;
}
.record-content-head{
		background: #f5faff;
    line-height: 60px;
    border-radius: 5px;
}
.record-content-head>div,.record-item>div {
		display: inline-block;
		text-align: center;
		width: 18%;
}
.record-item .make-operate{
		width: 10%;
}
.record-item{
			line-height: 55px;
		 font-size: 1.5vw;
}
.make-operate span{
			display: inline-block;
		background: #fc9453;
		line-height: 35px;
		width: 75%;
		color: #fff;
		border-radius: 5px;
}
.make-status.done{
		color: #3C9BFF;
}
.make-operate.done span{
			background: #fc9453;
}
.make-statu.unfinished{
		 color: #d43c33;
}
.make-operate.unfinished span{
			background: #3C9BFF;
}
.make-status .Finish{
		color: #3C9BFF;
}
.make-status .Pending{
		color: #d43c33;
}
.make-status .Canceled{
		color: #d43c33;
}
.make-operate .Pending{
			background:#3C9BFF;
}
.cov-date-caption{
		color:#3C9BFF !important;
}
.checked{
		background:#3C9BFF !important;
}
.cov-datepicker{
		border-color:#eee !important;
		padding: 0 0 0 10px !important;
		width: 150px !important;
		height: 40px !important;
		line-height: 40px !important;
}
